/* eslint-disable */
import React from "react";
import { FaBars } from "react-icons/fa";
import { SignOut } from "./SignOut";
import ReactTooltip from "react-tooltip";
import { Notification } from "./Notification";
import { IMAGES } from "../images";
import { Link } from "react-router-dom";
import { PATH } from "../../config";

export function Header() {
  document.body.style.backgroundColor = "#f5f5f5";
  return (
    <>
      <div className="header">
        {/* Logo  */}
        <div className="header-left">
          <Link to={PATH.DASHBOARD} className="logo">
            <img
              src={IMAGES.SAMA_WHITE_LOGO}
              alt="Logo"
              className="img-fluid monu_logo"
            />
          </Link>
        </div>
        <div className="PageName">
          <a href="javascript:void(0)" id="toggle_btn">
            <FaBars className="toggleset" />
          </a>
          {/* {auth?.sama_health_user?.userType === "admin" && (
            <>
              {
                location.pathname === PATH.DASHBOARD &&
                <h3 className="ml-4">{`Dashboard`}</h3>
              }
            </>
          )} */}
        </div>
        {/* /Logo */}
        {/* Mobile Menu Toggle */}
        <a href="javascript:void(0)" className="mobile_btn" id="mobile_btn">
          <FaBars />
        </a>
        {/* /Mobile Menu Toggle */}
        <ul className="nav user-menu">
          <li className="nav-item dropdown noti-dropdown pr-0 notification_dropdown">
            <Notification />
          </li>
          <li className="nav-item dropdown signOut-btn">
            <SignOut />
          </li>
        </ul>
        <ReactTooltip />
      </div>
    </>
  );
}
