import React, { useEffect } from "react";
import { useAuth } from "../../Navigation/Auth/ProvideAuth";
import { SidebarData } from "./SideBarData";
import SubMenu from "./SubMenu";
import '../js/jquery.slimscroll';
import $ from 'jquery';
export default function Sidebar() {
  let auth = useAuth();

  useEffect(() => {
    let $slimScrolls = $('.slimscroll');

    if ($slimScrolls.length > 0) {
      $slimScrolls.slimScroll({
        height: 'auto',
        width: '100%',
        position: 'right',
        size: '7px',
        color: '#ccc',
        wheelStep: 10,
        touchScrollStep: 100
      });
      var wHeight = $(window).height() - 60;
      $slimScrolls.height(wHeight);
      $('.sidebar .slimScrollDiv').height(wHeight);
      $(window).resize(function () {
        var rHeight = $(window).height() - 60;
        $slimScrolls.height(rHeight);
        $('.sidebar .slimScrollDiv').height(rHeight);
      });
    }

    function init() {
      $('#sidebar-menu a').on('click', function (e) {
        if ($(this).parent().hasClass('submenu')) {
          e.preventDefault();
        }
        if (!$(this).hasClass('subdrop')) {
          $('ul', $(this).parents('ul:first')).slideUp(350);
          $('a', $(this).parents('ul:first')).removeClass('subdrop');
          $(this).next('ul').slideDown(350);
          $(this).addClass('subdrop');
        } else if ($(this).hasClass('subdrop')) {
          $(this).removeClass('subdrop');
          $(this).next('ul').slideUp(350);
        }
      });
      $('#sidebar-menu ul li.submenu a.active').parents('li:last').children('a:first').addClass('active').trigger('click');
    }

    // Sidebar Initiate
    init();
  }, []);
  return (
    <div className="sidebar shadow" id="sidebar">
      <div className="sidebar-inner slimscroll">
        <div id="sidebar-menu" className="sidebar-menu">
          {/* <div className="header-left mb-0 sidebar-padding-logo">
            <Link to={PATH.DASHBOARD} className="logo full-logo">
              <img
                src={IMAGES.SAMA_WHITE_LOGO}
                alt="Logo"
                className="img-fluid monu_logo"
              />
            </Link>
            <Link to={PATH.DASHBOARD} className="mini-logo">
              <img
                src={IMAGES.SAMA_WHITE_LOGO}
                alt="Logo"
                className="img-fluid monu_logo"
              />
            </Link>
          </div> */}
          <ul className="sidebar-tabs-padding">
            {auth?.sama_health_user?.userType === "doctor" && (
              <React.Fragment>
                {SidebarData.map((item, index) => {
                  return <SubMenu key={index} item={item} />;
                })}
              </React.Fragment>
            )}
          </ul>
        </div>
      </div>
    </div>
  );
}