function request(actionType) {
  return {
    type: actionType,
  };
}

function success(actionType, response) {
  return {
    type: actionType,
    payload: response,
  };
}

function failure(actionType, error) {
  return {
    type: actionType,
    payload: error,
  };
}
const ADMIN_LOGIN = {
  ADMIN_LOGIN_REQUEST: "ADMIN_LOGIN_REQUEST",
  ADMIN_LOGIN_SUCCESS: "ADMIN_LOGIN_SUCCESS",
  ADMIN_LOGIN_FAILURE: "ADMIN_LOGIN_FAILURE",

  TWO_FA_CODE_REQUEST: "TWO_FA_CODE_REQUEST",
  TWO_FA_CODE_SUCCESS: "TWO_FA_CODE_SUCCESS",
  TWO_FA_CODE_FAILURE: "TWO_FA_CODE_FAILURE",
};

export {
  request,
  success,
  failure,
  ADMIN_LOGIN,
};
