import React from "react"

const NoPage = React.lazy(() => import("./NoPageFound"));
const login = React.lazy(() => import("./Login"));
const LandingPage = React.lazy(() => import("./LandingPage"));
const Dashboard = React.lazy(() => import("./Dashboard"));
const DoctorAppointment = React.lazy(() => import("./Doctor/DoctorAppointment"));
const DoctorPayments = React.lazy(() => import("./Doctor/DoctorPayments"));

const WEB_PAGES = {
  NOPAGE: NoPage,
  LOGIN: login,
  LANDING_PAGE: LandingPage,
  DASHBOARD: Dashboard,
  DOCTOR_APPOINTMENT: DoctorAppointment,
  DOCTOR_PAYMENTS: DoctorPayments,
};

export { WEB_PAGES };
